@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@800&display=swap');

:root{
    --primary: #00539CFF;
    --secondary: rgba(0, 83, 156, 0.61);
}

* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    border: none;
    outline: none;
}

body{
    width: 100dvw;
    background-color: var(--secondary);
}

li {
    list-style: none;
}

#root{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container{
    padding: 10px;
    border-radius: 5px;
    max-width: 1200px;
    width: 90%;
    display: flex;
    flex-direction: column;
    gap: 25px;
    align-items: center;
    justify-content: space-between;
}

.heading-input-container{
    max-width: 1000px;
    width: 100%;
    height: 35%;
}

h1{
    background-color: var(--primary);
    height: 125px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    letter-spacing: 3px;
    font-size: 3rem;
}

.input-btn-container input{
    height: 100px;
    width: 80%;
    text-align: center;
    font-size: 2rem;
}

.input-btn-container button{
    height: 100px;
    width: 20%;
    font-size: 2rem;
    color: var(--primary);
    background-color: whitesmoke;
    border-left: 8px solid var(--primary);
}

.input-btn-container button:hover{
    cursor: pointer;
    opacity: 0.7;
}

ul {
    padding-bottom: 25px;
    padding-top: 25px;
    max-width: 1000px;
    width: 100%;
    height: 35%;
    background-color: white;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.todo{
    width: 80%;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 2px solid black;
    padding-bottom: 15px;
}

.button-li-container{
    display: flex;
    align-items: center;
    gap:5px;
}

.button-li-container button{
    font-size: 1.5rem;
    height: 25px;
    width: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

.button-li-container button:hover{
    cursor: pointer;
}

.button-li-container li{
    font-size: 1.5rem;
    transition: 300ms ease-in-out all;
}

.delete{
    color: red;
    font-size: 1.5rem;
    cursor: pointer;
}

.delete:hover{
    color: rgb(82, 20, 20);
}

.buttons-container{
    max-width: 1000px;
    width: 100%;
    height: 35%;
    display: flex;
    gap: 55px;
    background-color: var(--primary);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    padding: 10px 0px;
}

.buttons-container button{
    outline: none;
    border: 1px solid white;
    color: white;
    background-color: var(--primary);
    font-size: 1.2rem;
    padding: 5px 15px;
}

.buttons-container button:hover{
    cursor: pointer;
    color: var(--primary);
    background-color: white;
}


.typewriter{
    overflow: hidden;
    white-space: nowrap;
    margin: 0;
    letter-spacing: 0.15em;
    animation: typing 1.5s steps(30, end);
  }
  
  @keyframes typing {
    from {
      width: 0;
    }
    to {
      width: 100%;
    }
  }


@media all and (max-width: 900px){
    h1{
        font-size: 2.5rem;
    }

    .input-btn-container{
        margin-top: 20px;
    }
    
    .input-btn-container input{
        font-size: 1.5rem;
    }
    
    .input-btn-container button{
        font-size: 1.5rem;
    }

    .button-li-container button{
        font-size: 1rem;
    }
    
    .button-li-container li{
        font-size: 1.2rem;
    }

    .buttons-container button{
        font-size: 1rem;
        padding: 5px 10px;
    }
}

@media all and (max-width: 700px){
    h1{
        font-size: 2rem;
    }

    .input-btn-container input{
        font-size: 1.3rem;
    }
    
    .input-btn-container button{
        font-size: 1.3rem;
    }

    .button-li-container button{
        font-size: 1rem;
    }
    
    .button-li-container li{
        font-size: 1rem;
    }

    .buttons-container{
        gap: 15px;
    }

    .buttons-container button{
        font-size: 1rem;
        padding: 5px 10px;
    }
}


@media all and (max-width: 700px){
    h1{
        height: 80px;
    }

    .input-btn-container input{
        font-size: 1.3rem;
        height: 50px;
    }
    
    .input-btn-container button{
        font-size: 1.3rem;
        height: 50px;
    }

    .button-li-container button{
        width: 20px;
        height: 20px;
    }
    
    .button-li-container li{
        font-size: 1rem;
    }

    .buttons-container{
        gap: 15px;
    }

    .buttons-container button{
        font-size: 0.9rem;
        padding: 5px
    }
}

@media all and (max-width: 500px){
    .button-li-container button{
        width: 15px;
        height: 15px;
        font-size: 0.5rem;
    }
    
    .button-li-container li{
        font-size: 0.9rem;
    }

    .delete{
        font-size: 0.9rem;
    }
}

@media all and (max-width: 400px){
    .button-li-container button{
        width: 10px;
        height: 10px;
    }
    
    .button-li-container li{
        font-size: 0.8rem;
    }

    .delete{
        font-size: 0.8rem;
    }
}

@media all and (max-width: 350px){
    .button-li-container li{
        font-size: 0.7rem;
    }

    .delete{
        font-size: 0.7rem;
    }
}




  
  
  
  